    url("//db.onlinewebfonts.com/t/940ace70d6c766e685a20dea33762abf.woff2") format("woff2"),
    url("//db.onlinewebfonts.com/t/940ace70d6c766e685a20dea33762abf.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/940ace70d6c766e685a20dea33762abf.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/940ace70d6c766e685a20dea33762abf.svg#American Typewriter") format("svg");
}

@font-face {font-family: "OPTICalculator";
    src: url("//db.onlinewebfonts.com/t/a4e40941cd04772dbe49aa5790ad5905.eot");
    src: url("//db.onlinewebfonts.com/t/a4e40941cd04772dbe49aa5790ad5905.eot?#iefix") format("embedded-opentype"),
    url("//db.onlinewebfonts.com/t/a4e40941cd04772dbe49aa5790ad5905.woff2") format("woff2"),
    url("//db.onlinewebfonts.com/t/a4e40941cd04772dbe49aa5790ad5905.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/a4e40941cd04772dbe49aa5790ad5905.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/a4e40941cd04772dbe49aa5790ad5905.svg#OPTICalculator") format("svg");
}

body {
  background-color: #282c34;
  text-align: center;
  margin: 0;
  padding: 0;
}
.App{
  text-align: center;
  background-color: #654321;
  width: 25vw;
  margin-left: 37.5vw;
  margin-top: 10vh;
  display:grid;
  grid-template-columns:repeat(4, 1fr);
  grid-auto-rows: minmax(50px, auto);
  padding: 15px;
  border-radius: 5px;
  border: 5px ridge 
}
.smallButtonFont {
  font-size: 20px;
}

#display{
  font-size: 25px;
}
#displayScreen{
  width: 22.5vw;
  background-color: rgb(0, 223, 0);
  text-align: right;
  padding: 10px;
  height: 40px;
  overflow-y: hidden;
  margin-bottom: 10px;
  border: 5px inset ;
  border-radius: 5px;
  font-family: 'OPTICalculator';
}
.infoBlock{
  display: flex;
  flex-direction: column;
}

.textLeft{
  text-align: left;
}
.textRight{
  text-align: Right;
}
.center{
  justify-content: center;
}
.textCenter{
  text-align: center;
}
#messageBox{
  display: flex;
  text-align: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
  font-size: 20px;
}

#displayScreen > p{
  /* display:grid;
  grid-column:1/4; */
  line-height: 10px;
  margin: 0;
  padding: 5;
}

#insideScreen{
  display: flex;
  /* border: 1px solid; */
  /* align-items: stretch; */
  justify-content: space-between;
}

#buttons{
  display:grid;
  grid-column:1/5;
  grid-template-columns:repeat(4, 1fr);
  grid-auto-rows: minmax(70px, auto);
  grid-gap: 10px;
}
button{
  font-family: 'American Typewriter';
  background-color: rgb(255, 226, 197);
  border-radius: 10px;
  border-color: #bb8652;
  outline: none;
 font-size: 30px;
}

button:active{
  background-color: rgba(255, 226, 197,0.5);
}
#clear{
  grid-column:1/3;
}

#multiply{
  grid-column:3/5;
}

#equals{
  grid-column:4/5;
  grid-row: 4/6;
}

#zero{
  grid-column:1/3;
}

@media (max-aspect-ratio: 10/10) and (max-width: 700px) {

  .App{
    height: 90vh;
    width: 89vw;
    margin: 0vw;
  }
  #displayScreen{
    width: 81vw;
  }
  #buttons{
    margin-top: -15vh;
  }
}

